import React from "react";
import useInput from "../../hooks/useInput";

import useTheme from "../../hooks/useTheme";
import FormUI from "../ui/FormUI";

const ColorScheme = () => {
  const themeName = useInput("");

  const mainBoldColor = useInput("#000000");
  const textColorAll = useInput("#000000");
  const textColorLink = useInput("#000000");
  const textColorError = useInput("#000000");
  const backgroundColor = useInput("#000000");
  const primary = useInput("#000000");
  const secondary = useInput("#000000");

  const [changeTheme, clearCacheThemes] = useTheme();

  const replaceTheme = (e, name, scheme) => {
    e.preventDefault();
    changeTheme(name, scheme);
  };

  const deleteTheme = (e, name) => {
    e.preventDefault();
    clearCacheThemes(name);
  };

  const colorPickerForm = {
    title: "ColorPicker",
    inputs: [
      {
        type: "text",
        placeholder: "theme name",
        value: themeName.value,
        onChange: themeName.onChange,
      },
      {
        type: "color",
        placeholder: "mainBoldColor",
        value: mainBoldColor.value,
        onChange: mainBoldColor.onChange,
      },
      {
        type: "color",
        placeholder: "textColorAll",
        value: textColorAll.value,
        onChange: textColorAll.onChange,
      },
      {
        type: "color",
        placeholder: "textColorLink",
        value: textColorLink.value,
        onChange: textColorLink.onChange,
      },
      {
        type: "color",
        placeholder: "textColorError",
        value: textColorError.value,
        onChange: textColorError.onChange,
      },
      {
        type: "color",
        placeholder: "backgroundColor",
        value: backgroundColor.value,
        onChange: backgroundColor.onChange,
      },
      {
        type: "color",
        placeholder: "primary",
        value: primary.value,
        onChange: primary.onChange,
      },
      {
        type: "color",
        placeholder: "secondary",
        value: secondary.value,
        onChange: secondary.onChange,
      },
    ],
    buttons: [
      {
        onClick: (e) => {
          replaceTheme(e);
          themeName.changeValue("");
          mainBoldColor.changeValue("");
          textColorAll.changeValue("");
          textColorLink.changeValue("");
          textColorError.changeValue("");
          backgroundColor.changeValue("");
          primary.changeValue("");
          secondary.changeValue("");
        },
        text: "change basic theme",
      },
      {
        onClick: (e) => replaceTheme(e, themeName.value),
        text: "load theme with name",
      },
      {
        onClick: (e) =>
          replaceTheme(e, themeName.value, {
            mainBoldColor: mainBoldColor.value,
            textColorAll: textColorAll.value,
            textColorLink: textColorLink.value,
            textColorError: textColorError.value,
            backgroundColor: backgroundColor.value,
            primary: primary.value,
            secondary: secondary.value,
          }),
        text: "load full name",
      },
      {
        onClick: (e) => deleteTheme(e, themeName),
        text: "delete theme name or delete all if theme name input is clear",
      },
    ],
  };

  return (
    <FormUI
      title={colorPickerForm.title}
      inputs={colorPickerForm.inputs}
      buttons={colorPickerForm.buttons}
    />
  );
};

export default ColorScheme;
