import useInput from "../../hooks/useInput";
import axios from "axios";
import Encrypt from "../../functions/Encrypt";

import FormUI from "../ui/FormUI";

const Register = (props) => {
  const { createAuth, changeSignForm, setError } = props;

  const username = useInput("");
  const email = useInput("");
  const password = useInput("");
  const repeatPassword = useInput("");

  const signUpFunc = (e) => {
    e.preventDefault();

    const validateEmail = (email) => {
      return email.match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
    };

    if (
      username.value !== "" &&
      email.value !== "" &&
      password.value !== "" &&
      repeatPassword.value !== ""
    ) {
      if (password.value !== repeatPassword.value) {
        setError("passwords are different");
        if (!validateEmail(email.value)) {
          setError("passwords are different and email is not correct");
        }
      } else {
        if (validateEmail(email.value)) {
          axios
            .get("http://localhost:9000/getPublicKey")
            .then((publicKey) =>
              Encrypt(
                `{"username": "${username.value}", "email": "${email.value}", "password": "${password.value}"}`,
                publicKey.data
              )
            )
            .then((data) => {
              axios
                .post("http://localhost:9000/register", { data: data })
                .then((res) => {

                  if (res.data.isLogin) {
                    setError("");
                    createAuth(res.data.token);

                  } else {

                    setError(
                      res.data?.error !== undefined
                        ? res.data?.error
                        : "error :( "
                    );
                  }
                })
                .catch((error) => {
                  setError(error);
                });
            })
            .catch((error) => {
              setError(error);
            });
        } else {
          setError("email is not correct");
        }
      }
    } else {
      setError("is empty?");
    }
  };

  const registerClick = (e) => {
    e.preventDefault();
    changeSignForm();
    setError("");
  };

  const registerForm = {
    title: "Register",
    inputs: [
      {
        type: "text",
        autocomplete: "username",
        placeholder: "Username",
        value: username.value,
        onChange: username.onChange,
      },
      {
        type: "text",
        autocomplete: "email",
        placeholder: "Email",
        value: email.value,
        onChange: email.onChange,
      },
      {
        type: "password",
        autocomplete: "current-password",
        placeholder: "Password",
        value: password.value,
        onChange: password.onChange,
      },
      {
        type: "password",
        autocomplete: "new-password",
        placeholder: "Repeat password",
        value: repeatPassword.value,
        onChange: repeatPassword.onChange,
      },
    ],
    buttons: [
      {
        onClick: signUpFunc,
        text: "sign up",
      },
      {
        onClick: registerClick,
        text: "already register ?",
      },
    ],
  };

  return (
    <FormUI
      title={registerForm.title}
      inputs={registerForm.inputs}
      buttons={registerForm.buttons}
    />
  );
};

export default Register;
