import useInput from "../../hooks/useInput";
import axios from "axios";
import Encrypt from "../../functions/Encrypt";
import FormUI from "../ui/FormUI";

const Login = (props) => {
  const { createAuth, changeSignForm, setError } = props;
  const username = useInput("");
  const password = useInput("");

  const singInFunc = (e) => {
    e.preventDefault();
    if (username.value !== "" && password.value !== "") {
      axios
        .get("http://localhost:9000/getPublicKey")
        .then((publicKey) =>
          Encrypt(
            `{"username": "${username.value}", "password": "${password.value}"}`,
            publicKey.data
          )
        )
        .then((data) => {
          axios
            .post(`http://localhost:9000/login`, { data: data })
            .then((res) => {
              if (res.data.isLogin) {
                setError("");
                if (res.data.token !== undefined) {
                  createAuth(res.data.token);
                } else {
                  setError("user has undefined");
                }
              } else {
                setError("wrong username or/and password");
              }
            })
            .catch((error) => {
              setError(error);
            });
        })
        .catch((error) => {
          setError(error);
        });
    } else {
      setError("is empty?");
    }
  };

  const loginClick = (e) => {
    e.preventDefault();
    changeSignForm();
    setError("");
  };

  const loginForm = {
    title: "Login",
    inputs: [
      {
        type: "text",
        placeholder: "Username",
        autocomplete: "username",
        value: username.value,
        onChange: username.onChange,
      },
      {
        type: "password",
        placeholder: "Password",
        autocomplete: "current-password",
        value: password.value,
        onChange: password.onChange,
      },
    ],
    buttons: [
      {
        onClick: singInFunc,
        text: "sign in",
      },
      {
        onClick: loginClick,
        text: "not register ?",
      },
    ],
  };

  return (
    <FormUI
      title={loginForm.title}
      inputs={loginForm.inputs}
      buttons={loginForm.buttons}
    />
  );
};

export default Login;
