


const auth = (props) => {
    const {navigate, setIsLogin} = props;

    const createAuth = (token) => {
        setIsLogin(true);
        localStorage.setItem("userToken", token);
        navigate("/", {replace: true});
    };

    const deleteAuth = () => {
        setIsLogin(false);
        localStorage.removeItem("userToken");
        navigate("/", {replace: true});
    };

    return {createAuth, deleteAuth};
}




export default auth;